    answer: '–ú—ã –ø—Ä–æ–∏–∑–≤–æ–¥–∏–º –±–∞–ª–∫–∏ –¥–ª–∏–Ω–æ–π –æ—Ç 1 –¥–æ 12 –º–µ—Ç—Ä–æ–≤, –≤—ã—Å–æ—Ç–æ–π –æ—Ç 200 –¥–æ 500 –º–º, —à–∏—Ä–∏–Ω–æ–π –ø–æ–ª–∫–∏ –æ—Ç 80 –¥–æ 200 –º–º. –í–æ–∑–º–æ–∂–Ω–æ –∏–∑–≥–æ—Ç–æ–≤–ª–µ–Ω–∏–µ –ø–æ –∏–Ω–¥–∏–≤–∏–¥—É–∞–ª—å–Ω—ã–º —Ä–∞–∑–º–µ—Ä–∞–º.',
