    answer: '–°—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–µ —Ä–∞–∑–º–µ—Ä—ã ‚Äî –æ—Ç 3 –¥–æ 7 —Ä–∞–±–æ—á–∏—Ö –¥–Ω–µ–π. –ò–Ω–¥–∏–≤–∏–¥—É–∞–ª—å–Ω—ã–µ –∑–∞–∫–∞–∑—ã ‚Äî –æ—Ç 10 –¥–Ω–µ–π. –î–æ—Å—Ç–∞–≤–∫–∞ –ø–æ —Ä–µ–≥–∏–æ–Ω—É ‚Äî 1‚Äì3 –¥–Ω—è, –ø–æ –†–æ—Å—Å–∏–∏ ‚Äî –æ—Ç 5 –¥–Ω–µ–π –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ä–µ–≥–∏–æ–Ω–∞.',
