    question: '–ö–∞–∫–∏–µ –ø–æ—Ä–æ–¥—ã –¥—Ä–µ–≤–µ—Å–∏–Ω—ã –∏—Å–ø–æ–ª—å–∑—É—é—Ç—Å—è?',
