import React, { useState, useEffect } from 'react';
import CustomSelect from '../../ui/CustomSelect/CustomSelect';
import './RequestFormSection.css';

const PROJECT_OPTIONS = [
  { value: 'house', label: '–ñ–∏–ª–æ–π –¥–æ–º' },
  { value: 'commercial', label: '–ö–æ–º–º–µ—Ä—á–µ—Å–∫–æ–µ –∑–¥–∞–Ω–∏–µ' },
  { value: 'banya', label: '–ë–∞–Ω—è' },
  { value: 'other', label: '–î—Ä—É–≥–æ–µ' },
];

const RequestFormSection = ({ calculatorSummary = null }) => {
  const [projectType, setProjectType] = useState('');
  const [comment, setComment] = useState(calculatorSummary || '');
  const [validationError, setValidationError] = useState(false);

  useEffect(() => {
    if (calculatorSummary) setComment(calculatorSummary);
  }, [calculatorSummary]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!projectType) {
      setValidationError(true);
      return;
    }
    setValidationError(false);
    // TODO: –∑–∞–º–µ–Ω–∏—Ç—å alert –Ω–∞ —Ä–µ–∞–ª—å–Ω—É—é –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã/–∏–Ω—Ç–µ–≥—Ä–∞—Ü–∏—é —Å backend/CRM
    alert('–ó–∞—è–≤–∫–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞! –ú—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.');
  };

  return (
    <section className="request-form">
      <div className="container">
        <h2>–û—Å—Ç–∞–≤–∏—Ç—å –∑–∞—è–≤–∫—É –Ω–∞ —Ä–∞—Å—á—ë—Ç</h2>
        <form className="request-form__form" onSubmit={handleSubmit}>
          <div className="form-row">
            <div className="form-group">
              <label>–í–∞—à–µ –∏–º—è *</label>
              <input type="text" required />
            </div>
            <div className="form-group">
              <label>–¢–µ–ª–µ—Ñ–æ–Ω *</label>
              <input type="tel" required />
            </div>
          </div>
          <div className="form-group">
            <label>Email</label>
            <input type="email" />
          </div>
          <div className={`form-group ${validationError ? 'form-group--error' : ''}`}>
            <CustomSelect
              label="–¢–∏–ø –ø—Ä–æ–µ–∫—Ç–∞"
              value={projectType}
              options={PROJECT_OPTIONS}
              onChange={(v) => { setProjectType(v); setValidationError(false); }}
              placeholder="–í—ã–±–µ—Ä–∏—Ç–µ —Ç–∏–ø –ø—Ä–æ–µ–∫—Ç–∞"
              className={validationError ? 'custom-select--error' : ''}
            />
            {validationError && <span className="request-form__error">–í—ã–±–µ—Ä–∏—Ç–µ —Ç–∏–ø –ø—Ä–æ–µ–∫—Ç–∞</span>}
          </div>
          <div className="form-group">
            <label>–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π {calculatorSummary && <span className="request-form__calc-badge">–†–∞—Å—á—ë—Ç –∏–∑ –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–∞</span>}</label>
            <textarea rows="4" value={comment} onChange={(e) => setComment(e.target.value)} placeholder="–û–ø–∏—à–∏—Ç–µ –≤–∞—à –ø—Ä–æ–µ–∫—Ç –∏–ª–∏ –≤—Å—Ç–∞–≤—å—Ç–µ —Ä–∞—Å—á—ë—Ç –∏–∑ –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–∞" />
          </div>
          <div className="form-group">
            <label className="checkbox-label">
              <input type="checkbox" required />
              <p className="checkbox-text">–°–æ–≥–ª–∞—Å–µ–Ω –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö</p>              
            </label>
          </div>
          <button type="submit" className="btn-primary">
            –û—Ç–ø—Ä–∞–≤–∏—Ç—å –∑–∞—è–≤–∫—É
          </button>
        </form>
      </div>
    </section>
  );
};

export default RequestFormSection;

