import React, { useState, useCallback, useEffect } from 'react';
import './WhatIsBdkSection.css';

import balkaBdk from '../../../images/balka_bdk.png';
import bdkBalki from '../../../images/bdk_balki.jpg';
import bdkBalki1 from '../../../images/bdk_balki_1.jpg';
import bdkBalki2 from '../../../images/bdk_balki_2.jpg';

const SLIDES = [
  { src: balkaBdk, alt: '–ë–∞–ª–∫–∞ –ë–î–ö ‚Äî –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–æ' },
  { src: bdkBalki, alt: '–ë–∞–ª–∫–∏ –ë–î–ö' },
  { src: bdkBalki1, alt: '–ë–∞–ª–∫–∏ –ë–î–ö –≤ —Å—Ç—Ä–æ–∏—Ç–µ–ª—å—Å—Ç–≤–µ' },
  { src: bdkBalki2, alt: '–ë–∞–ª–∫–∏ –ë–î–ö' },
];

const WhatIsBdkSection = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const goPrev = useCallback(() => {
    setCurrentIndex((i) => (i <= 0 ? SLIDES.length - 1 : i - 1));
  }, []);

  const goNext = useCallback(() => {
    setCurrentIndex((i) => (i >= SLIDES.length - 1 ? 0 : i + 1));
  }, []);

  useEffect(() => {
    const id = setInterval(goNext, 5000);
    return () => clearInterval(id);
  }, [goNext]);

  return (
    <section className="what-is-bdk">
      <div className="container">
        <h2>–ß—Ç–æ —Ç–∞–∫–æ–µ –±–∞–ª–∫–∏ –ë–î–ö?</h2>
        <div className="what-is-bdk__content">
          <div className="what-is-bdk__text">
            <p>
              –ë–î–ö (–±–∞–ª–∫–∞ –¥–≤—É—Ç–∞–≤—Ä–æ–≤–∞—è –∫–ª–µ—ë–Ω–∞—è) ‚Äî —ç—Ç–æ —Å–æ–≤—Ä–µ–º–µ–Ω–Ω—ã–π —Å—Ç—Ä–æ–∏—Ç–µ–ª—å–Ω—ã–π –º–∞—Ç–µ—Ä–∏–∞–ª
              –∏–∑ –¥—Ä–µ–≤–µ—Å–∏–Ω—ã, –∫–æ—Ç–æ—Ä—ã–π —Å–æ—á–µ—Ç–∞–µ—Ç –≤ —Å–µ–±–µ –ø—Ä–æ—á–Ω–æ—Å—Ç—å, —ç–∫–æ–ª–æ–≥–∏—á–Ω–æ—Å—Ç—å –∏
              —ç–∫–æ–Ω–æ–º–∏—á–Ω–æ—Å—Ç—å.
            </p>
            <h3>–ü—Ä–µ–∏–º—É—â–µ—Å—Ç–≤–∞ –ë–î–ö:</h3>
            <ul>
              <li>–í—ã—Å–æ–∫–∞—è –Ω–µ—Å—É—â–∞—è —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç—å</li>
              <li>–≠–∫–æ–ª–æ–≥–∏—á–µ—Å–∫–∏ —á–∏—Å—Ç—ã–π –º–∞—Ç–µ—Ä–∏–∞–ª</li>
              <li>–î–æ–ª–≥–æ–≤–µ—á–Ω–æ—Å—Ç—å –∏ –Ω–∞–¥—ë–∂–Ω–æ—Å—Ç—å</li>
              <li>–í–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å —Å–æ–∑–¥–∞–Ω–∏—è –±–æ–ª—å—à–∏—Ö –ø—Ä–æ–ª—ë—Ç–æ–≤</li>
            </ul>
          </div>

          <div className="what-is-bdk__image">
            {SLIDES.map((slide, idx) => (
              <div
                key={idx}
                className="what-is-bdk__slide"
                aria-hidden={currentIndex !== idx}
                style={{
                  opacity: currentIndex === idx ? 1 : 0,
                  pointerEvents: currentIndex === idx ? 'auto' : 'none',
                  zIndex: currentIndex === idx ? 1 : 0,
                }}
              >
                <img
                  src={slide.src}
                  alt={slide.alt}
                  className="what-is-bdk__img"
                  loading="lazy"
                  decoding="async"
                />
              </div>
            ))}
            <button
              type="button"
              className="what-is-bdk__slider-btn what-is-bdk__slider-btn--prev"
              aria-label="–ü—Ä–µ–¥—ã–¥—É—â–∏–π —Å–ª–∞–π–¥"
              onClick={goPrev}
            >
              <svg className="what-is-bdk__slider-btn-icon" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" aria-hidden="true">
                <path d="M15 18l-6-6 6-6" />
              </svg>
            </button>
            <button
              type="button"
              className="what-is-bdk__slider-btn what-is-bdk__slider-btn--next"
              aria-label="–°–ª–µ–¥—É—é—â–∏–π —Å–ª–∞–π–¥"
              onClick={goNext}
            >
              <svg className="what-is-bdk__slider-btn-icon" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" aria-hidden="true">
                <path d="M9 18l6-6-6-6" />
              </svg>
            </button>
            <div className="what-is-bdk__dots" role="tablist" aria-label="–°–ª–∞–π–¥—ã">
              {SLIDES.map((_, idx) => (
                <button
                  key={idx}
                  type="button"
                  role="tab"
                  aria-selected={idx === currentIndex}
                  aria-label={`–°–ª–∞–π–¥ ${idx + 1}`}
                  className={`what-is-bdk__dot ${idx === currentIndex ? 'what-is-bdk__dot--active' : ''}`}
                  onClick={() => setCurrentIndex(idx)}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default WhatIsBdkSection;
